"use client";

import Image from "next/image";
import styles from "./UserInfo.module.scss";
import BottomIcon from "@/assets/images/bottom-icon.svg";
import { useEffect, useState } from "react";

interface UserInfoProps {
  name: string;
  position: string;
  avatarUrl: string;
  onClick?: () => void;
}

const UserInfo = ({ name, position, avatarUrl, onClick }: UserInfoProps) => {
  const [isMobile, setIsMobile] = useState(false);
  const [isDropdownOpen, setIsDropdownOpen] = useState(false); // Состояние для отслеживания открытия дропдауна

  useEffect(() => {
    const handleResize = () => {
      setIsMobile(window.innerWidth <= 768);
    };
    window.addEventListener("resize", handleResize);
    handleResize();
    return () => window.removeEventListener("resize", handleResize);
  }, []);

  const handleClick = () => {
    if (onClick) {
      onClick(); // Вызов переданного обработчика
    }
    setIsDropdownOpen(!isDropdownOpen); // Переключение состояния дропдауна
  };

  return (
    <>
      {isMobile ? (
        <div onClick={handleClick}>Профиль</div>
      ) : (
        <div className={styles.userInfo} onClick={handleClick}>
          <div className={styles.user}>
            <Image
              src={avatarUrl}
              alt="User avatar"
              width={44}
              height={44}
              className={styles.userAvatar}
            />
            <div className={styles.userDetails}>
              <div className={styles.userName}>{name}</div>
              <div className={styles.userPosition}>{position}</div>
            </div>
          </div>
          <img
            src={BottomIcon.src}
            alt="Bottom icon"
            className={`${styles.img} ${isDropdownOpen ? styles.rotate : ""}`}
          />
        </div>
      )}
    </>
  );
};

export default UserInfo;
