'use client'

import styles from '@/styles/MainPage/header.module.scss'
import SearchInput from '@/components/UI/Input/SearchInput'
import UserInfo from '@/components/UI/UserInfo/UserInfo'
import UserAvatar from '@/assets/images/user.jpg'
import Button from '@/components/UI/Button/Button'
import Logo from '../../assets/images/logo.svg'
import { useEffect, useState, useRef } from 'react'
import Image from 'next/image'
import DropDownMenu from '../UI/DropDownMenu/dropDown'
import { useRouter } from 'next/navigation'

const Header = () => {
	const router = useRouter()
	const [isMobile, setIsMobile] = useState(false)
	const [isExpanded, setIsExpanded] = useState(false)
	const dropdownRef = useRef<HTMLDivElement>(null)

	const toggleMenu = () => setIsExpanded(!isExpanded)

	useEffect(() => {
		const handleClickOutside = (event: MouseEvent) => {
			if (
				dropdownRef.current &&
				!dropdownRef.current.contains(event.target as Node)
			) {
				setIsExpanded(false)
			}
		}

		document.addEventListener('mousedown', handleClickOutside)
		return () => document.removeEventListener('mousedown', handleClickOutside)
	}, [])

	useEffect(() => {
		const handleResize = () => {
			setIsMobile(window.innerWidth <= 768)
		}
		window.addEventListener('resize', handleResize)
		handleResize()
		return () => window.removeEventListener('resize', handleResize)
	}, [])

	return (
		<>
			{isMobile ? (
				<div className={styles.mobile_header}>
					<Image width={140} height={36} src={Logo.src} alt='Logo' />
					<div>ghghg</div>
				</div>
			) : (
				<div className={styles.header}>
					<div className={styles.input_wrapper}>
						<SearchInput
							placeholder='Поиск сотрудников по ФИО, отделу, телефону'
							onChange={value => console.log('Search:', value)}
						/>
					</div>
					<div className={styles.button__wrapper}>
						<Button onClick={() => router.push('/emergency_number')}>
							Экстренные номера
						</Button>
					</div>
					<div className={styles.userInfoContainer} ref={dropdownRef}>
						<UserInfo
							name='Петрова Анна Максимовна'
							position='Слесарь 1 категории'
							avatarUrl={UserAvatar.src}
							onClick={toggleMenu}
						/>
						{isExpanded && <DropDownMenu />}
					</div>
				</div>
			)}
		</>
	)
}

export default Header
